.card{
    margin: auto;
    width: 500px;
    height: 500px;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    background: url('../img/login6.jpg');
    background-size: cover;
    box-shadow: 3px 5px 5px rgba(3, 32, 51, .8);
    overflow: hidden;
}

.content{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: rgba(0, 0, 0, .4);
    opacity: 0;
    transition: .5s;
}

.content h2,a{
    color: rgba(255, 255, 255, .8);
    margin: 10px 0;
}

.card:hover .content{
    opacity: 1;
}